@media screen and (max-width: 670px) {
  .grid {
    display: grid;
    grid-template: 
      "intro" max-content
      "subscription" 1fr 
      "why" 1fr
      / 1fr
      ;  
    margin-top: 2em;
}
.subscription, .why {
  padding: 40px;
  max-width: 100%;
}
}

@media screen and (max-width: 405px) {
  .intro {
    grid-area: intro;
    padding: 0 20px;
    h1{
      font-size: 1.05em;
    }
    h2 {
      font-size: 0.8em;
    }
    p{
      font-size: 0.685em;
    }
  }
  .subscription{
    span:nth-child(1) {
      font-size: 1.4em;
    }
    span:nth-child(2) {
      font-size: 0.7em;
    }
    p{
      font-size: 0.8em;
    }
    .buttom {
      padding: 0.6rem !important;
    }
  }

  .why {
    font-size: 0.860em;
  }
  .subscription, .why {
    h2 {
      font-size: 0.9em;
    }
    padding: 20px;
    max-width: 100%;
  }
}
