body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  font-size: 1em;
  font-family: 'Karla', sans-serif;
  background-color: $cor-secondary-1;
}

.grid {
  display: grid;
  grid-template: 
    "intro intro" max-content
    "subscription why" 1fr
    / 1fr 1fr
    ;  
  max-width: 628px;
  margin: 0 auto;
  background: #fff;
  border-radius: 8px;
  margin-top: 2em;
  overflow: hidden;
  box-shadow: 2px 2px 10px hsl(218, 22%, 67%);
}

.intro {
  grid-area: intro;
  padding: 0 40px;
  h1{
    font-size: 1.5em;
    font-weight: 700;
    color: $cor-primary-1;
    margin-top: 40px;
  }
  h2 {
    font-size: 1.3em;
    font-weight: 700;
    color: $cor-primary-2;
    margin-bottom: 4px;
  }
  p {
    max-width: 500px;
    font-size: 0.9em;
    color: $cor-secondary-2;
    font-weight: 400;
    padding-bottom: 40px;
  }
}

.subscription, .why {
  padding: 40px;
  max-width: 314px;
  h2{
    font-size: 1.2em;
    color: $cor-secondary-1;
    margin-bottom: 20px;
  }
}
//subscription 

.subscription {
  grid-area: subscription;
  background: rgba(75, 178, 175, 1);
  color: $cor-secondary-1;
  .dollar {
    font-size: 2em;
    font-weight: 700;
    line-height: 1;
  }
  .month {
    font-size: 1em;
    font-weight: 400;
    color: #bfc2d6;
    padding-left: 10px;
  }
  p{
    font-size: 0.9em;
    margin-top: 10px;
    margin-bottom: 30px;
  }
  .buttom {
    background: $cor-primary-2;
    width: 100%;
    padding: 1rem;
    font-size: 1em;
    color: $cor-secondary-1;
    font-weight: 700;
    border: 1px solid $cor-primary-1;
    border-radius: 8px;
    cursor: pointer;
  }
}

.price {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

//why

.why {
  grid-area: why;
  background: rgba(75, 178, 175, 0.8);
  color: $cor-secondary-1;
  p{
    font-size: 0.8em;
  }
}

.attribution {
  max-width: 628px;
  margin: 0 auto;
  text-align: center;
  color: $cor-secondary-2;
  margin: 20px auto;
}

